import { FC } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { pages } from "./Pages";

export const PageRouter: FC = () => {
  return (
    <>
      <BrowserRouter>
        <Routes>
          {pages.map((page) => {
            return (
              <Route key={page.title} path={page.path} element={<page.element />} />
            );
          })}
        </Routes>
      </BrowserRouter>
    </>
  );
};